import styled from 'styled-components';
import { useState } from 'react';

const Button = styled.button`
  width: 50%;
`

export const SpeechInput = (props) => {
  const setText = props.setText; // API„Å´Ê∏°„Åô„ÉÜ„Ç≠„Çπ„Éà„Çí„Çª„ÉÉ„Éà„Åô„Çã
  const setReply = props.setReply; // API„Åã„Çâ„ÅÆ„É¨„Çπ„Éù„É≥„Çπ„Çí„Çª„ÉÉ„Éà„Åô„Çã

  const [speech, setSpeech] = useState('üé§');

  const formdata = new FormData();
  formdata.append('apikey', process.env.REACT_APP_A3RT_API_KEY);
  
  const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
  const recognition = new SpeechRecognition();
  recognition.lang = 'ja-JP';
  recognition.interimResults = true;

  recognition.onaudiostart = () => {
    setSpeech('üó£');
    setText('(„Åó„ÇÉ„Åπ„Å£„Å¶)');
  }

  recognition.onresult = (event) => {
    setSpeech('üé§');
    setText(event.results[0][0].transcript);
    // Áô∫Ë®Ä„ÅåÁµÇ‰∫Ü„Åó„Åü„Åã„Å©„ÅÜ„Åã
    if (event.results[0].isFinal) {
      formdata.append('query', event.results[0][0].transcript);
      SpeechToBot();
    }
  }

  recognition.onerror = (event) => {
    console.log('error: ' + event);
  }
  
  const SpeechToBot = async () => {
    setReply('„Éª„Éª„Éª');
    fetch('https://api.a3rt.recruit-tech.co.jp/talk/v1/smalltalk', {
      method: 'post',
      body: formdata,
    }).then(response => {
      response.json().then(data => {
        setReply(data.results[0].reply);
      })
    }).catch(e => console.log(e));
  }

  return (
    <>
      <Button type="button" onClick={() => recognition.start()}>{ speech }</Button>
    </>
  )
}